import React from "react";
import "./view.style.css";
import LinkView from "../Components/LinkView";
import { signOutWithGoogle } from "../firebase/firebase.util";
import { GetLinks, GetUserData } from "../firebase/firebase.util";
import { withRouter, useParams } from "react-router-dom";

var linkComps, userImg, userName;

class View extends React.Component {
  constructor(props) {
    super();
    this.state = {
      links: null,
      userData: null
    };
    GetLinks(props.match.params.userId).then((links) => {
      this.setState({ links: links });
    });
    GetUserData(props.match.params.userId).then((data) => {
      this.setState({ userData: data });
    });
  }

  render() {
    if (this.state.links === null) {
      linkComps = "fdtdt";
    } else {
      linkComps = this.state.links.map((data) => (
        <LinkView link={data.link} text={data.text} />
      ));
    }

    if (this.state.userData === null) {
      userImg = "";
      userName = "";
    } else {
      userImg = (
        <img
          className="user-dp"
          src={this.state.userData.photoURL}
          alt="user-pic"
        />
      );
      userName = <p className="user-name">{this.state.userData.displayName}</p>;
    }
    return (
      <div>
        {userImg}
        {userName}

        <div className="linkscontainer">{linkComps}</div>
      </div>
    );
  }
}

export default withRouter(View);
